import React from 'react'
import Layout from '../components/MyLayout';
import {withRouter} from 'next/router';

const Content = (props) => (
    <div>
      <h1>{props.router.query.title}</h1>
      <p>This is the blog post content.</p>
    </div>
  )
  
  const Post = withRouter((props) => (
      <Layout>
         <Content />
      </Layout>
  ))

export default Post
